import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import { t } from '../../../i18n';
import Draggable from 'react-draggable';
import {
  setActiveTab,
  setFitBoundFlag,
  setSelectedMark,
} from '../../../redux/geoLocalisation/localisations.actions';
import { GEOLOCALISATION_NAMESPACE } from '../constants';
import BranchTab from './BranchTab';
import GabTab from './GabTab';

const options = [
  { value: 1, label: t(`${GEOLOCALISATION_NAMESPACE}ivoryCoastAtlanticBank`) },
  { value: 2, label: t(`${GEOLOCALISATION_NAMESPACE}beninAtlanticBank`) },
  { value: 3, label: t(`${GEOLOCALISATION_NAMESPACE}togoAtlanticBank`) },
  { value: 4, label: t(`${GEOLOCALISATION_NAMESPACE}burkinaFasoAtlanticBank`) },
  { value: 5, label: t(`${GEOLOCALISATION_NAMESPACE}nigerAtlanticBank`) },
  { value: 6, label: t(`${GEOLOCALISATION_NAMESPACE}maliAtlanticBank`) },
  { value: 7, label: t(`${GEOLOCALISATION_NAMESPACE}senegalAtlanticBank`) },
  {
    value: 10,
    label: t(`${GEOLOCALISATION_NAMESPACE}guineaBissauAtlanticBank`),
  },
];
class MapTable extends React.Component {
  constructor() {
    super();
    this.state = {
      showTab: true,
    };
  }
  render() {
    return (
      <Draggable>
        <div className="list-map">
          <div className="h2">{t(`${GEOLOCALISATION_NAMESPACE}search`)}</div>
          <div className="tab-container">
            <ul className="nav nav-tabs">
              <li
                onClick={() => {
                  this.props.setActiveTab('branch');
                  this.props.setFitBoundFlag(true);
                  this.props.setSelectedMark(null);
                }}
                className={this.props.activeTab === 'branch' ? 'active' : ''}
              >
                <a>
                  <span className="icon">
                    {t(`${GEOLOCALISATION_NAMESPACE}agency`)}
                  </span>
                </a>
              </li>
              <li
                onClick={() => {
                  this.props.setActiveTab('gab');
                  this.props.setFitBoundFlag(true);
                  this.props.setSelectedMark(null);
                }}
                className={this.props.activeTab === 'branch' ? '' : 'active'}
              >
                <a>
                  <span className="icon">
                    {t(`${GEOLOCALISATION_NAMESPACE}gab`)}
                  </span>
                </a>
              </li>
            </ul>
            <div className="tab-content">
              {this.props.activeTab === 'gab' ? (
                <GabTab options={options} />
              ) : (
                <BranchTab options={options} />
              )}
            </div>
          </div>
        </div>
      </Draggable>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  setActiveTab: tab => {
    dispatch(setActiveTab(tab));
  },
  setFitBoundFlag: value => {
    dispatch(setFitBoundFlag(value));
  },
  setSelectedMark: mark => {
    dispatch(setSelectedMark(mark));
  },
});

const mapStateToProps = state => ({
  activeTab: state.GeoLocalisations.activeTab,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(MapTable),
);
