import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import {
  GoogleMap,
  withScriptjs,
  withGoogleMap,
  Marker,
  InfoWindow,
  BicyclingLayer,
} from 'react-google-maps';
import { t } from '../../../i18n';
import { GEOLOCALISATION_NAMESPACE } from '../constants';
import {
  setSelectedMark,
  setHoveredMark,
  setFitBoundFlag,
  setMapZoom,
} from '../../../redux/geoLocalisation/localisations.actions';
const styles = require('../styles/GoogleMapStyles.json');

const Map = props => {
  let map;
  const bounds = new window.google.maps.LatLngBounds();
  const data =
    props.activeTab === 'gab'
      ? props.gabFilteredLocations.length < 1
        ? props.gabsInfo
        : props.gabFilteredLocations
      : props.branchFilteredLocations.length < 1
      ? props.branchesInfo
      : props.branchFilteredLocations;
  data.map(element => {
    const latitude = element.latitude;
    const longitude = element.longitude;
    const latLng = new window.google.maps.LatLng(latitude, longitude);
    bounds.extend(latLng);
  });

  return (
    <GoogleMap
      ref={ref => {
        map = ref;
      }}
      defaultZoom={10}
      zoom={props.mapZoom}
      defaultClickableIcons
      clickableIcons
      defaultOptions={{
        disableDefaultUI: true, // disable default map UI
        draggable: true, // make map draggable
        keyboardShortcuts: false, // disable keyboard shortcuts
        scaleControl: true, // allow scale controle
        scrollwheel: true, // allow scroll wheel
        styles: styles, // change default map styles
      }}
      onClick={() => {
        props.setSelectedMark(null);
      }}
      onIdle={() => {
        if (props.fitBoundFlag) {
          map.fitBounds(bounds);
        }
        if (props.selectedMark) {
          const latitude = props.selectedMark.latitude;
          const longitude = props.selectedMark.longitude;
          const latLng = new window.google.maps.LatLng(latitude, longitude);
          map.panTo(latLng);
          props.setFitBoundFlag(false);
        }
      }}
    >
      <BicyclingLayer autoUpdate />
      {props.activeTab === 'branch'
        ? data.map(branch =>
            props.hoveredMark === branch.branchId ? (
              <Marker
                animation={1}
                key={branch.branchId}
                position={{
                  lat: branch.latitude,
                  lng: branch.longitude,
                }}
                onClick={() => {
                  props.setSelectedMark(branch);
                }}
                onMouseOver={() => {
                  props.setHoveredMark(branch.branchId);
                }}
                onFocus={() => {
                  props.setHoveredMark(branch.branchId);
                }}
                onMouseOut={() => {
                  props.setHoveredMark(null);
                }}
                onBlur={() => {
                  props.setHoveredMark(null);
                }}
                icon={{
                  url: require('../../../assets/img/mapIcons/marker-map.png'),
                  scaledSize: new window.google.maps.Size(48, 60),
                }}
              />
            ) : (
              <Marker
                animation={4}
                key={branch.branchId}
                position={{
                  lat: branch.latitude,
                  lng: branch.longitude,
                }}
                onClick={() => {
                  map.panTo(
                    new window.google.maps.LatLng(
                      branch.latitude,
                      branch.longitude,
                    ),
                  );
                  props.setSelectedMark(branch);
                }}
                onMouseOver={() => {
                  props.setHoveredMark(branch.branchId);
                }}
                onFocus={() => {
                  props.setHoveredMark(branch.branchId);
                }}
                onMouseOut={() => {
                  props.setHoveredMark(null);
                }}
                onBlur={() => {
                  props.setHoveredMark(null);
                }}
                icon={{
                  url: require('../../../assets/img/mapIcons/marker-map.png'),
                  scaledSize: new window.google.maps.Size(40, 50),
                }}
              />
            ),
          )
        : data.map(gab =>
            props.hoveredMark === gab.gabId ? (
              <Marker
                animation={1}
                key={gab.gabId}
                position={{
                  lat: gab.latitude,
                  lng: gab.longitude,
                }}
                onClick={() => {
                  map.panTo(
                    new window.google.maps.LatLng(gab.latitude, gab.longitude),
                  );
                  props.setSelectedMark(gab);
                }}
                onMouseOver={() => {
                  props.setHoveredMark(gab.gabId);
                }}
                onFocus={() => {
                  props.setHoveredMark(gab.gabId);
                }}
                onMouseOut={() => {
                  props.setHoveredMark(null);
                }}
                onBlur={() => {
                  props.setHoveredMark(null);
                }}
                icon={{
                  url: require('../../../assets/img/mapIcons/marker-gab.png'),
                  scaledSize: new window.google.maps.Size(48, 63),
                }}
              />
            ) : (
              <Marker
                animation={4}
                key={gab.gabId}
                position={{
                  lat: gab.latitude,
                  lng: gab.longitude,
                }}
                onClick={() => {
                  map.panTo(
                    new window.google.maps.LatLng(gab.latitude, gab.longitude),
                  );
                  props.setSelectedMark(gab);
                }}
                onMouseOver={() => {
                  props.setHoveredMark(gab.gabId);
                }}
                onFocus={() => {
                  props.setHoveredMark(gab.gabId);
                }}
                onMouseOut={() => {
                  props.setHoveredMark(null);
                }}
                onBlur={() => {
                  props.setHoveredMark(null);
                }}
                icon={{
                  url: require('../../../assets/img/mapIcons/marker-gab.png'),
                  scaledSize: new window.google.maps.Size(40, 53),
                }}
              />
            ),
          )}

      {props.selectedMark && (
        <InfoWindow
          position={{
            lat: props.selectedMark.latitude,
            lng: props.selectedMark.longitude,
          }}
          onCloseClick={() => {
            props.setSelectedMark(null);
          }}
        >
          <div className="popin-map">
            <h2>{props.selectedMark.name}</h2>
            <p className="adress">
              {t(`${GEOLOCALISATION_NAMESPACE}address`)}:
              {props.selectedMark.address}
            </p>
            {props.activeTab === 'branch' ? (
              <>
                <p className="fax">
                  {t(`${GEOLOCALISATION_NAMESPACE}fax`)}:
                  {props.selectedMark.fax}
                </p>
                <p className="tel">
                  {t(`${GEOLOCALISATION_NAMESPACE}phone`)}:
                  {props.selectedMark.tel}
                </p>
                <p className="hour">
                  {t(`${GEOLOCALISATION_NAMESPACE}schedule`)}:
                  {props.selectedMark.horaire}
                </p>
              </>
            ) : null}
          </div>
        </InfoWindow>
      )}
    </GoogleMap>
  );
};

const WrappedMap = withScriptjs(withGoogleMap(Map));
const mapBlock = props => (
  <WrappedMap
    googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.REACT_APP_GOOGLE_KEY}&region=MA`}
    loadingElement={<div style={{ height: `100%` }} />}
    containerElement={<div style={{ height: `900px` }} />}
    mapElement={<div style={{ height: `100%` }} />}
    gabsInfo={props.gabsInfo}
    branchesInfo={props.branchesInfo}
    branchFilteredLocations={props.branchFilteredLocations}
    gabFilteredLocations={props.gabFilteredLocations}
    activeTab={props.activeTab}
    hoveredMark={props.hoveredMark}
    selectedMark={props.selectedMark}
    setHoveredMark={mark => props.setHoveredMark(mark)}
    setSelectedMark={mark => props.setSelectedMark(mark)}
    setFitBoundFlag={value => props.setFitBoundFlag(value)}
    setMapZoom={value => props.setMapZoom(value)}
    fitBoundFlag={props.fitBoundFlag}
    mapZoom={props.mapZoom}
  />
);
const mapDispatchToProps = dispatch => ({
  setSelectedMark: mark => {
    dispatch(setSelectedMark(mark));
  },
  setHoveredMark: mark => {
    dispatch(setHoveredMark(mark));
  },
  setFitBoundFlag: value => {
    dispatch(setFitBoundFlag(value));
  },
  setMapZoom: value => {
    dispatch(setMapZoom(value));
  },
});
const mapStateToProps = state => ({
  gabsInfo: state.GeoLocalisations.gabsInfo,
  branchesInfo: state.GeoLocalisations.branchesInfo,
  branchFilteredLocations: state.GeoLocalisations.branchFilteredLocations,
  gabFilteredLocations: state.GeoLocalisations.gabFilteredLocations,
  activeTab: state.GeoLocalisations.activeTab,
  selectedMark: state.GeoLocalisations.selectedMark,
  hoveredMark: state.GeoLocalisations.hoveredMark,
  fitBoundFlag: state.GeoLocalisations.fitBoundFlag,
  mapZoom: state.GeoLocalisations.mapZoom,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(mapBlock),
);
